function calcGradeLevel(rank,total){
  let rate = rank/total;
  let level="";
  if(rate<= 0.01)
    level = "A1";
  else if(rate <= 0.03)
    level = "A2";
  else if(rate <= 0.06)
    level = "A3";
  else if(rate <= 0.1)
    level = "A4";
  else if(rate <= 0.15)
    level = "A5";   
  else if(rate <= 0.21)
    level = "B1"; 
  else if(rate <= 0.28)
    level = "B2"; 
  else if(rate <= 0.36)
    level = "B3"; 
  else if(rate <= 0.43)
    level = "B4"; 
  else if(rate <= 0.50)
    level = "B5"; 
  else if(rate <= 0.57)
    level = "C1"; 
  else if(rate <= 0.64)
    level = "C2"; 
  else if(rate <= 0.71)
    level = "C3"; 
  else if(rate <= 0.78)
    level = "C4"; 
  else if(rate <= 0.84)
    level = "C5";
  else if(rate <= 0.89)
    level = "D1"; 
  else if(rate <= 0.93)
    level = "D2"; 
  else if(rate <= 0.96)
    level = "D3"; 
  else if(rate <= 0.98)
    level = "D4"; 
  else if(rate <= 0.99)
    level = "D5";
  else
    level = "E";

  return level;
}

export default {
  queryUserInfo,
  getSMSCode,
  userLogin,
  phoneLogin,
  userLogout,
  queryExamCount,
  queryExamList,
  querySubjectInfo,
  querySubjectGrade,
  queryAnswerCard,
  queryQuestionTHs,
  queryQuestionKownledge,
  calcGradeLevel,
};



